.neck {
    height: 10vh;
    margin: 0 2vw 0 9vw;
    width: 89vw;
}

.empty_body {
    height: 80vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: "Aclonica";
    color: grey;
    font-size: 4rem;
}

.request {
    margin-bottom: 0.5%;
    margin-top: 0.5%;
    transition: margin 0.2s linear, transform 0.2s;
}

.selected_request {
    margin-bottom: 3%;
    margin-top: 3%;
    transform: scale(1.1);
    filter: drop-shadow(4px 4px 4px rgba(0, 0, 0, 0.25));
}

.selected_request:first-child {
    margin-bottom: 3%;
    margin-top: 0.5%;
    transform: scale(1.1);
    filter: drop-shadow(4px 4px 4px rgba(0, 0, 0, 0.25));
}

.full_request {
    width: 90%;
    margin: 0% 5% 2% 5%;
}

.header {
    display: flex;
    flex-direction: row;
}

.header_title {
    border-radius: 10px;
    padding: 10px;
    width: 100%;
    background-color: #0085ff;
    color: white;
    font-weight: bold;
}

.header_key,
.header_value {
    margin: 5px;
    padding: 5px;
}

.request_body {
    margin: 5px;
    padding: 5px;
}

.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: grey;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

input:checked + .slider {
    background-color: #0085ff;
}

input:focus + .slider {
    box-shadow: 0 0 1px #0085ff;
}

input:checked + .slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}

.create_mocks {
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 100px;
    background-color: #0085ff;
    color: white;
    transition: border-width 0.2s linear, width 0.2s linear, height 0.2s linear,
        box-shadow 0.2s linear;
    text-overflow: ellipsis;
    white-space: nowrap;
    cursor: pointer;
    overflow: hidden;
    width: 13vh;
    height: 13vh;
    box-shadow: 16px 16px 16px 8px rgb(0, 0, 0, 0.25);
    z-index: 10;
    border: 1.5vh double white;
}

.create_mocks .plus_span {
    font-size: 5rem;
    font-weight: bold;
    transition: font-size 0.2s linear;
}

.create_mocks:hover {
    width: 10vh;
    height: 10vh;
    z-index: 10;
    border: 3vh double white;
}
