.one-mock {
    position: absolute;
    display: flex;
    justify-content: center;
    bottom: 0px;
    width: 100vw;
    font-family: "Aclonica";
    font-style: normal;
    font-weight: bold;
    font-size: 100px;
    color: white;
    z-index: 10;
}

.gradient {
    position: relative;
    width: 100%;
    height: 100%;
    left: 0px;
    top: 0px;
    background-color: #0085ff;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}
